
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
data() {
return {
money: this.$route.params.money,
amount: "",
disabled: true,
isShow: true
};
},
methods: {

draw() {
this.amount = this.money;
},

submit() {
console.log("提交");
/* -------------
发送给后台的代码写在这里
----------- */
this.money -= this.amount;
this.amount = "";



},

change() {

if (this.amount != "" && this.check(this.amount)) {
this.disabled = false;
if (Number(this.amount) > Number(this.money)) {
this.isShow = false;
this.disabled = true;
} else {
this.disabled = false;
this.isShow = true;
}
} else if (Number(this.amount) > Number(this.money)) {
this.isShow = false;
this.disabled = true;
} else {
this.disabled = true;
}
},

check(str) {
var myreg = /^([1-9][\d]{0,7}|0)(\.[\d]{1,2})?$/;
if (!myreg.test(str)) {
return false;
} else {
return true;
}
}
},
components: { XInput, XButton, Group }
};



